ul {
  list-style-type: none;
}

li a,
.dropbtn {
  display: inline-block;
  color: #ecae59;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

li a:hover,
.dropdown:hover .dropbtn {
  color: #ffdeb2;
}

li.dropdown {
  display: inline-block;
}

.dropdown-content {
  background-image: url(/GameOfGlenn/static/media/cool-menu.13aaf573.svg);
  background-repeat: no-repeat;
  display: none;
  position: absolute;
  transform: translate(-23px, 0);
  width: 150px;
  height: 190px;
  z-index: 1;
  color: #ecae59;
  padding-top: 0.5px;
  margin-top: -1px;
  font-family: "Didact Gothic", sans-serif;
}

.dropdown-content div {
  position: relative;
  padding: 0px 0px 0px 0px;
  text-decoration: none;
  display: block;
  text-align: left;
  font-family: "Didact Gothic", sans-serif;
  margin-left: -13px;
  margin-top: 16px;
  text-align: center;
}

.dropdown-content :hover {
  color: rgb(255, 255, 255);
  cursor: pointer;
}

.dropdown:hover .dropdown-content {
  display: block;
}

@media screen and (max-width: 500px) {
  ul {
    float: none;
    display: block;
    text-align: left;
  }
}

