form {
	max-width: 450px;
	margin: 0 auto;
}

form > span> label {
    color: #ECAE59
}

form > .space-for-val {
    width: 100%;
    height: 1.2em;
    margin-top: 3px;
    display: flex;
    justify-content: space-between;
}


form>button {
	display: block;
	width: 101%;
	margin: 20px 0;
	background: rgb(64, 128, 101);
	color: white;
	padding: 15px 20px 15px 0px;
	border: 3px solid #eee;
	cursor: pointer;
}

form input {
	border: 3px solid #eee;
	width: 100%;
	padding: 15px 0px 15px 0px;
	background: #eee;
}

form input:focus {
	outline: 0;
	background: white;
}

.space-for-val {
	height: 1.2em;
	margin-top: 3px;
}



form input[type="range"] {
	padding: 0;
	-webkit-appearance: none;
	border: 0px;
	overflow: hidden;
	height: 12px;
	width: 101%;
	cursor: pointer;
	border-radius: 0;
	border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
	/* iOS */
	outline: none;
	/* Remove outline */
}


form input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 12px;
    width: 9px;
    background:  rgb(221, 163, 36);
    border: 4px solid rgb(0, 0, 0);
    -webkit-appearance: none;
    box-shadow: -100vw 0 0 100vw #ECAE59;
    outline: none; /* Remove outline */
}


input[type="range"] {
	-webkit-appearance: none;
	position: relative;
	overflow: hidden;
	height: 20px;
	width: 350;
	margin-left: 10px;
	margin-right: 10px;
	border-radius: 0;
	outline: none;
}

::-webkit-slider-runnable-track {
	background: #889395;
}

::-webkit-slider-thumb {
	-webkit-appearance: none;
	width: 20px;
	height: 40px;
	background: #ECAE59;
	box-shadow: -100vw 0 0 100vw #ECAE59;
	border: 2px solid #ECAE59;
	outline: none;
}
.add-popup {
    position: fixed;
    z-index: 1;
    padding-top: 2%;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0,0,0,0.5);
  }
  

  .add-popup-content {
    position: relative;
    background-color: #2B4045;
    margin: auto;
    padding: 0;
    width: 50%;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2),0 6px 20px 0 rgba(0,0,0,0.19);
    -webkit-animation-name: animatetop;
    -webkit-animation-duration: 0.4s;
    animation-name: animatetop;
    animation-duration: 0.4s
  }
  
  @-webkit-keyframes animatetop {
    from {top:-300px; opacity:0} 
    to {top:0; opacity:1}
  }
  
  @keyframes animatetop {
    from {top:-300px; opacity:0}
    to {top:0; opacity:1}
  }
  
  .close {
    color: white;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }
  
  .add-popup-header {
    text-align: center;
    padding: 2px 16px;
    background-color: #2B4045;
    color: white;
    border-bottom: 3px solid #ECAE59;
    border-top: 3px solid #ECAE59;
    font-family:"Game of Thrones";
  }
  
  .add-popup-body {
    background: linear-gradient(0deg, #1E3032 19.84%, #374C4F 71.64%);
    padding: 2px 16px;}
  
  .add-popup-footer {
    text-align: center;
    font-family: 'Didact Gothic', sans-serif;
    font-size: 1em;
    padding: 15px 16px;
    height: 15px;
    background-color:  #2B4045;
    color: white;
    border-bottom: 3px solid #ECAE59;
    border-top: 3px solid #ECAE59;
  }

  .span-col-whi {
    color: white;
  }
  
  .span-col-ora {
    color: #ECAE59
  }


  @media screen and (max-width: 800px) {
    .add-popup-content {
      width: 90%;
    }
  }
