.flexRowShow {
	display: flex;
	flex-direction: column;
	align-items: center;
	font-family: 'Didact Gothic', sans-serif;
}

.valid {
	border-color: rgb(37, 211, 110);
}

.invalid {
	border-color: rgb(230, 81, 81);
}

.btn-add {
	font-family: 'Didact Gothic', sans-serif;
}

form>select {
	display: block;
	width: 101%;
	outline: 0;
	background: #eee;
	padding: 15px 20px 15px 0px;
	border: 3px solid #eee;
}

form>textarea {
	display: block;
	width: 95%;
	outline: 0;
	background: #eee;
	padding: 3px 20px 15px 0px;
	border: 3px solid #eee;
}

form>button:hover {
	opacity: 0.8;
}

form>button:disabled {
	background-color: rgb(171, 189, 196);
	cursor: not-allowed;
	color: rgb(255, 255, 255);
}


form {
	max-width: 450px;
	margin: 0 auto;
}

form > span> label {
    color: #ECAE59
}

form > .space-for-val {
    width: 100%;
    height: 1.2em;
    margin-top: 3px;
    display: flex;
    justify-content: space-between;
}


form>button {
	display: block;
	width: 101%;
	margin: 20px 0;
	background: rgb(64, 128, 101);
	color: white;
	padding: 15px 20px 15px 0px;
	border: 3px solid #eee;
	cursor: pointer;
}

form input {
	border: 3px solid #eee;
	width: 100%;
	padding: 15px 0px 15px 0px;
	background: #eee;
}

form input:focus {
	outline: 0;
	background: white;
}

.space-for-val {
	height: 1.2em;
	margin-top: 3px;
}



form input[type="range"] {
	padding: 0;
	-webkit-appearance: none;
	border: 0px;
	overflow: hidden;
	height: 12px;
	width: 101%;
	cursor: pointer;
	border-radius: 0;
	border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
	/* iOS */
	outline: none;
	/* Remove outline */
}


form input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 12px;
    width: 9px;
    background:  rgb(221, 163, 36);
    border: 4px solid rgb(0, 0, 0);
    -webkit-appearance: none;
    box-shadow: -100vw 0 0 100vw #ECAE59;
    outline: none; /* Remove outline */
}


input[type="range"] {
	-webkit-appearance: none;
	position: relative;
	overflow: hidden;
	height: 20px;
	width: 350;
	margin-left: 10px;
	margin-right: 10px;
	border-radius: 0;
	outline: none;
}

::-webkit-slider-runnable-track {
	background: #889395;
}

::-webkit-slider-thumb {
	-webkit-appearance: none;
	width: 20px;
	height: 40px;
	background: #ECAE59;
	box-shadow: -100vw 0 0 100vw #ECAE59;
	border: 2px solid #ECAE59;
	outline: none;
}